<template>
    <div class="home container">
        <Title title="Магазины" description="Список магазинов в которых мы собираем акции и скидки"></Title>
        <DonorCardSet :donors="donors" class="donors" />
    </div>
</template>

<style>
    .donors {
        margin-top: 45px;
    }
</style>

<script>
import Title from '@/components/UI/Title.vue'
import DonorCardSet from '@/components/cardSets/DonorCardSet.vue'
import DonorService from '@/services/DonorService'


export default {
    components: {
        Title,
        DonorCardSet
    },
    data() {
        return {
            donors: []
        }
    },
    async created() {
        await this.pushDonors()
    },
    methods: {
        async pushDonors() {
            const donors = await DonorService.getDonors()
            this.donors = donors
        }
    }
}
</script>
